*{
    box-sizing: border-box;
}


body{
    margin:0 auto;
    padding: 0;
    max-width:960px;
    font-size:16px;
    line-height:1.5;
}
/* Flex */

.inline-flex{
    max-width:600px;
    display:flex;
    align-items:center;
    justify-content: space-around;
    margin-bottom:0.5rem;
}
.inline-flex button{
    margin-right: 1rem;
    /* vertical-align:center; */
}
.inline-flex li{
    margin-right:1rem;
}

/* Form */
.form-group{
    margin:12px 0;
}
form legend{
    font-size:1.2rem;
}
.form-group label{
    margin-right: 0.5rem;
}
.form-group input[type='text']{
    border-radius:3px;
    border:1px solid #989898;
}
.form-group input[type='text']:focus{
    border:1px solid    
}

/* Button */
button{
    color:white;
    cursor:pointer;
    border:none;
    text-align:center;
    text-decoration:none;
    padding:0.5rem;
    border-radius:3px;
    transition-duration:0.4s;
}
.btn-primary{
    background-color:#008CBA;
}
.btn-danger{
    background-color:#f44336;
}
.btn-prime{
    background-color:#00b2ed;
}
.btn-green{
    background-color:#4CAF50;
}
button:hover{
    opacity:0.8;
}


